/* App1.css */

/* Style for links across the top */
nav {
    display: flex;
    justify-content: center;
}

    nav ul {
        display: flex; /* Set the display to flex */
        flex-direction: row; /* Arrange items horizontally */
        list-style: none;
        padding: 0;
    }

        nav ul li {
            margin: 0 10px;
        }

            /* Style the links like buttons */
            nav ul li a {
                display: inline-block;
                padding: 10px 20px;
                background-color: #007bff; /* Button color */
                color: #fff; /* Text color */
                text-decoration: none;
                border-radius: 5px;
                transition: background-color 0.3s ease;
            }

                nav ul li a:hover {
                    background-color: #ce1212; /* Darker button color on hover */
                }

/* Remove bullet points */
ul {
    list-style: none;
}

/* Center the header */
header {
    text-align: center;
}

/* Change background color */
body {
    background-color: #fff; /* Change this color to your desired background color */
}

/* styles.css */

/* Center align the content */
.container {
    text-align: center;
}

/* Title style */
.title {
    font-size: 24px;
    margin-bottom: 20px;
}

/* Search input style */
.search-input {
    padding: 10px;
    border-radius: 20px;
    border: 1px solid #ccc;
    width: 300px;
    margin-right: 10px;
    outline: none;
}

/* Search button style */
.search-button {
    padding: 10px 20px;
    border-radius: 20px;
    border: none;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
    outline: none;
}

/* Matched images container style */
.matched-images {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

/* Matched image card style */
.image-card {
    margin: 10px;
}

/* Image style */
.image {
    max-width: 200px;
    max-height: 200px;
}

/* Image name style */
.image-name {
    margin-top: 5px;
}

/* Add to cart button style */
.add-to-cart-button {
    padding: 8px 16px;
    border-radius: 20px;
    border: none;
    background-color: #28a745;
    color: #fff;
    cursor: pointer;
    outline: none;
    margin-right: 8px;
}

/* Download button style */
.download-button {
    padding: 8px 16px;
    border-radius: 20px;
    border: none;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
    outline: none;
}

/* Cart container style */
.cart-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
    justify-content: center;
}

/* Cart item style */
.cart-item {
    margin: 10px;
}

/* Remove from cart button style */
.remove-from-cart-button {
    padding: 8px 16px;
    border-radius: 20px;
    border: none;
    background-color: #dc3545;
    color: #fff;
    cursor: pointer;
    outline: none;
    margin-right: 8px;
}

/* Clear cart button style */
.clear-cart-button {
    padding: 10px 20px;
    border-radius: 20px;
    border: none;
    background-color: #ffc107;
    color: #212529;
    cursor: pointer;
    outline: none;
    margin-right: 8px;
}

/* Download cart button style */
.download-cart-button {
    padding: 10px 20px;
    border-radius: 20px;
    border: none;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
    outline: none;
}
